<template>
  <div>
    <el-form
      :model="ruleForm"
      :rules="rules"
      ref="ruleForm"
      label-width="50px"
      class="demo-ruleForm"
    >
      <el-form-item label="账号" prop="identity">
        <el-input v-model="ruleForm.identity"></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input type='password' v-model="ruleForm.password"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('ruleForm')">登陆</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  name: 'login',
  data() {
    return {
      ruleForm: {
        identity: '',
        password: '',
      },
      rules: {
        identity: [
          { required: true, message: '请输入账号', trigger: 'blur' },
          { pattern: /^(?=.*[a-zA-Z])(?=.*\d)[^]{6,11}$/, 
            message: '长度在6到10个字符，必须包含字母和数字', trigger: 'blur' },
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { pattern: /^(?=.*[a-zA-Z])(?=.*\d)[^]{6,11}$/,
            message: '长度在6到10个字符，必须包含字母和数字', trigger: 'blur' }
        ],
      }
    };
  },
  methods: {
    submitForm(loginForm) {
      this.$refs[loginForm].validate((valid) => {
        if (valid) {
          let identity = this[loginForm].identity;
          let password = this[loginForm].password;

          this.$store.dispatch('login', { identity, password })
          .then(() => { this.$router.push('/main/identity'); })
          .catch((err) => { console.log(err); });
        } else {
          console.log('error submit!!');
        }
      });
    } 
  }
}
</script>


